html {
    font-size: 16px;
}

a {
    color: inherit;
}
body {
    font-family: 'Roboto', sans-serif;
}

.header{
&__background {
    background: url(../images/cover.jpg);
    height: 100vh;
    background-size: cover;
    color: #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    padding-bottom: 53px;
}
    
}
.wrap-hamburguer {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 44px;
    height: 44px;
    background-color:#0271bc;
}
.hamburguer {
    width: 16px;
    height: 14px;
}
.header__background--title {
    font-size: 42px;   
    font-weight: 700;
    margin: 0px;
    margin-bottom: 10px;
    

}

.span {
    font-size: 16px;
}
.arrow1 {
    border-radius: 50%;
    width: 45px;
    height: 45px;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 45px;
}
.tips {
    padding: 30px 15px 45px 15px;
   
}
.section__tips {
    color: #8ccbea;
    font-size: 14px;
}
.section__title {
    font-weight: bold;
    font-size: 24px;
}
.section__paragraph {
    color: #4a4a4a;
    font-size: 18px;
}
.go {
    color: #f26002;
    width: 76px;
    height: 45px;
    border-radius: 22.5px;
    border: 2px solid #f26002;
    background-color: #ffffff;
}
.reasons-container {
    text-align: center;
}
.reasons {
    text-align: center;
    background-color: #eff9ff;
    padding-top: 30px;
    padding-bottom: 60px;
}
.more-reasons {
    color: #ffffff;
    background-color: #f26002;
    border-radius: 22.5px;
    border: 2px solid #f26002;
    width: 184px;
    height: 45px;
    padding:0px 30px;
}
.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #304268;
    color: #ffffff;
    padding-top: 60px;
    padding-bottom: 45px;
    position: relative;
}
.list-item {
    text-align: center;
    padding: 5px;
}
.arrow2 {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 45px;
    height: 45px;
    background-color: #ffffff;
    border-radius: 50%;
    position: absolute;
    top: -5%;
   
}
.copy {
    display: block;
    text-align: center;
    padding: 5px
}



